
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
}


.navbar {
  width: 100%;
  background-color: #34495e;
  padding: 15px;
  display: flex;
  justify-content: space-between; 
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
}


.logo a {
  color: #fff;
  text-decoration: none;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: color 0.3s;
}

.logo a:hover {
  color: #2ecc71; 
}


.nav-links {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  width: 40%;
}

.nav-links li {
  margin: 0 20px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  padding: 8px 16px;
  transition: background-color 0.3s, color 0.3s;
}

.nav-links a:hover {
  background-color: #2ecc71;
  color: white;
  border-radius: 5px;
}


.content {
  margin-top: 100px; 
  padding: 20px;
  text-align: center;
}

.content h1 {
  font-size: 36px;
  margin-bottom: 20px;
}

.content p {
  font-size: 18px;
}
